import React from 'react';
import {
    Row,
    Col,
    Card,
    CardBody,
    CardTitle,
    Dropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
    Input
} from 'reactstrap';
import {ChevronUp, ChevronDown} from 'lucide-react';

const DeliveryImpactDashboard: React.FC = () => {
    const [activeTab, setActiveTab] = React.useState<string>('Overview');
    const [filtersOpen, setFiltersOpen] = React.useState<{
        projects: boolean;
        teams: boolean;
    }>({
        projects: false,
        teams: false
    });

    const toggleFilter = (filter: 'projects' | 'teams') => {
        setFiltersOpen({
            ...filtersOpen,
            [filter]: !filtersOpen[filter]
        });
    };

    return (
        <>
            <div className="dashboard-header-controls">
                <h1 className="dashboard-title">Delivery Impact Dashboard</h1>
                <div className="filters">
                    <Dropdown
                        isOpen={filtersOpen.projects}
                        toggle={() => toggleFilter('projects')}
                        className="filter-item"
                    >
                        <DropdownToggle caret>All Projects</DropdownToggle>
                        <DropdownMenu>
                            <DropdownItem>All Projects</DropdownItem>
                            <DropdownItem>Digital Transformation</DropdownItem>
                            <DropdownItem>Platform Migration</DropdownItem>
                            <DropdownItem>Process Automation</DropdownItem>
                            <DropdownItem>System Upgrade</DropdownItem>
                        </DropdownMenu>
                    </Dropdown>

                    <Dropdown
                        isOpen={filtersOpen.teams}
                        toggle={() => toggleFilter('teams')}
                        className="filter-item"
                    >
                        <DropdownToggle caret>All Teams</DropdownToggle>
                        <DropdownMenu>
                            <DropdownItem>All Teams</DropdownItem>
                            <DropdownItem>Engineering</DropdownItem>
                            <DropdownItem>Marketing</DropdownItem>
                            <DropdownItem>Sales</DropdownItem>
                            <DropdownItem>Customer Service</DropdownItem>
                            <DropdownItem>HR</DropdownItem>
                        </DropdownMenu>
                    </Dropdown>

                    <div className="filter-item">
                        <Input type="month" defaultValue="2025-03" />
                    </div>
                </div>
            </div>

            <Row className="metrics-cards">
                <Col md={3}>
                    <Card className="metric-card">
                        <CardBody>
                            <CardTitle tag="div" className="card-title">
                                Active Projects
                            </CardTitle>
                            <div className="card-value">12</div>
                            <div className="trend up">
                                <ChevronUp size={16} />
                                <span>2 from last month</span>
                            </div>
                        </CardBody>
                    </Card>
                </Col>

                <Col md={3}>
                    <Card className="metric-card">
                        <CardBody>
                            <CardTitle tag="div" className="card-title">
                                Team Change Saturation
                            </CardTitle>
                            <div className="card-value">72%</div>
                            <div className="trend down">
                                <ChevronDown size={16} />
                                <span>5% from last month</span>
                            </div>
                        </CardBody>
                    </Card>
                </Col>

                <Col md={3}>
                    <Card className="metric-card">
                        <CardBody>
                            <CardTitle tag="div" className="card-title">
                                Resource Allocation
                            </CardTitle>
                            <div className="card-value">85%</div>
                            <div className="trend up">
                                <ChevronUp size={16} />
                                <span>7% from last month</span>
                            </div>
                        </CardBody>
                    </Card>
                </Col>

                <Col md={3}>
                    <Card className="metric-card">
                        <CardBody>
                            <CardTitle tag="div" className="card-title">
                                Identified Risks
                            </CardTitle>
                            <div className="card-value">7</div>
                            <div className="trend up">
                                <ChevronUp size={16} />
                                <span>3 from last month</span>
                            </div>
                        </CardBody>
                    </Card>
                </Col>
            </Row>

            <Row className="main-content">
                <Col md={3}>
                    <div className="panel">
                        <div className="panel-header">Impacted Teams</div>
                        <div className="panel-body">
                            <ul className="team-list">
                                <li className="team-item active">
                                    <div className="team-name">Engineering</div>
                                    <div className="team-meta">
                                        <span>5 Projects</span>
                                        <span>8 Members</span>
                                        <span className="alert-badge">3</span>
                                    </div>
                                </li>
                                <li className="team-item">
                                    <div className="team-name">Marketing</div>
                                    <div className="team-meta">
                                        <span>3 Projects</span>
                                        <span>6 Members</span>
                                        <span className="alert-badge">1</span>
                                    </div>
                                </li>
                                <li className="team-item">
                                    <div className="team-name">Sales</div>
                                    <div className="team-meta">
                                        <span>2 Projects</span>
                                        <span>12 Members</span>
                                    </div>
                                </li>
                                <li className="team-item">
                                    <div className="team-name">
                                        Customer Service
                                    </div>
                                    <div className="team-meta">
                                        <span>4 Projects</span>
                                        <span>15 Members</span>
                                        <span className="alert-badge">2</span>
                                    </div>
                                </li>
                                <li className="team-item">
                                    <div className="team-name">HR</div>
                                    <div className="team-meta">
                                        <span>1 Project</span>
                                        <span>3 Members</span>
                                    </div>
                                </li>
                                <li className="team-item">
                                    <div className="team-name">Finance</div>
                                    <div className="team-meta">
                                        <span>2 Projects</span>
                                        <span>5 Members</span>
                                        <span className="alert-badge">1</span>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </Col>

                <Col md={6}>
                    <div className="panel impact-map">
                        <div className="panel-header">Impact Connections</div>
                        <div className="impact-connections">
                            <div className="entity-column teams">
                                <h3>Delivery Teams</h3>
                                <div className="entity-item team">
                                    Engineering
                                </div>
                                <div className="entity-item team">
                                    Marketing
                                </div>
                                <div className="entity-item team">Sales</div>
                            </div>

                            <div className="projects-column">
                                <div className="project-item">
                                    <div className="project-header">
                                        <div className="project-name">
                                            Digital Transformation
                                        </div>
                                        <div className="project-status in-progress">
                                            In Progress
                                        </div>
                                    </div>
                                    <div className="project-details">
                                        <span>Start: Jan 15</span>
                                        <span>End: Jun 30</span>
                                        <span>PM: Michael Chen</span>
                                    </div>
                                    <div className="project-confidence">
                                        <div
                                            className="confidence-level medium"
                                            style={{width: '65%'}}
                                        ></div>
                                    </div>
                                </div>

                                <div className="project-item">
                                    <div className="project-header">
                                        <div className="project-name">
                                            Platform Migration
                                        </div>
                                        <div className="project-status at-risk">
                                            At Risk
                                        </div>
                                    </div>
                                    <div className="project-details">
                                        <span>Start: Feb 1</span>
                                        <span>End: May 15</span>
                                        <span>PM: Jessica Lee</span>
                                    </div>
                                    <div className="project-confidence">
                                        <div
                                            className="confidence-level low"
                                            style={{width: '35%'}}
                                        ></div>
                                    </div>
                                </div>

                                <div className="project-item">
                                    <div className="project-header">
                                        <div className="project-name">
                                            Process Automation
                                        </div>
                                        <div className="project-status on-track">
                                            On Track
                                        </div>
                                    </div>
                                    <div className="project-details">
                                        <span>Start: Mar 1</span>
                                        <span>End: Apr 30</span>
                                        <span>PM: David Wilson</span>
                                    </div>
                                    <div className="project-confidence">
                                        <div
                                            className="confidence-level high"
                                            style={{width: '90%'}}
                                        ></div>
                                    </div>
                                </div>
                            </div>

                            <div className="entity-column individuals">
                                <h3>Destination</h3>
                                <div className="entity-item individual">
                                    Software Dev
                                </div>
                                <div className="entity-item individual">
                                    QA Testers
                                </div>
                                <div className="entity-item individual">
                                    DevOps
                                </div>
                                <div className="entity-item individual">
                                    UX Design
                                </div>
                            </div>

                            <div className="connection-lines">
                                <svg>
                                    {/* Engineering to Digital Transformation */}
                                    <path
                                        className="connection-line"
                                        d="M120,115 C150,115 150,115 190,130"
                                        fill="none"
                                    />

                                    {/* Engineering to Platform Migration */}
                                    <path
                                        className="connection-line"
                                        d="M120,115 C150,115 150,205 190,210"
                                        fill="none"
                                    />

                                    {/* Marketing to Digital Transformation */}
                                    <path
                                        className="connection-line"
                                        d="M120,205 C150,205 150,130 190,130"
                                        fill="none"
                                    />

                                    {/* Marketing to Process Automation */}
                                    <path
                                        className="connection-line"
                                        d="M120,205 C150,205 150,290 190,290"
                                        fill="none"
                                    />

                                    {/* Sales to Process Automation */}
                                    <path
                                        className="connection-line"
                                        d="M120,295 C150,295 150,290 190,290"
                                        fill="none"
                                    />

                                    {/* Digital Transformation to All Individuals */}
                                    <path
                                        className="connection-line"
                                        d="M450,130 C480,130 480,115 510,115"
                                        fill="none"
                                    />
                                    <path
                                        className="connection-line"
                                        d="M450,130 C480,130 480,205 510,205"
                                        fill="none"
                                    />
                                    <path
                                        className="connection-line"
                                        d="M450,130 C480,130 480,295 510,295"
                                        fill="none"
                                    />
                                    <path
                                        className="connection-line"
                                        d="M450,130 C480,130 480,385 510,385"
                                        fill="none"
                                    />

                                    {/* Platform Migration to Software Dev and QA */}
                                    <path
                                        className="connection-line"
                                        d="M450,210 C480,210 480,115 510,115"
                                        fill="none"
                                    />
                                    <path
                                        className="connection-line"
                                        d="M450,210 C480,210 480,205 510,205"
                                        fill="none"
                                    />

                                    {/* Process Automation to DevOps and UX */}
                                    <path
                                        className="connection-line"
                                        d="M450,290 C480,290 480,295 510,295"
                                        fill="none"
                                    />
                                    <path
                                        className="connection-line"
                                        d="M450,290 C480,290 480,385 510,385"
                                        fill="none"
                                    />
                                </svg>
                            </div>
                        </div>
                    </div>
                </Col>

                <Col md={3}>
                    <div className="panel">
                        <div className="panel-header">Team Member Impact</div>
                        <div className="panel-body">
                            <div className="tabs">
                                <div
                                    className={`tab ${activeTab === 'Overview' ? 'active' : ''}`}
                                    onClick={() => setActiveTab('Overview')}
                                >
                                    Overview
                                </div>
                                <div
                                    className={`tab ${activeTab === 'Risks' ? 'active' : ''}`}
                                    onClick={() => setActiveTab('Risks')}
                                >
                                    Risks
                                </div>
                                <div
                                    className={`tab ${activeTab === 'Updates' ? 'active' : ''}`}
                                    onClick={() => setActiveTab('Updates')}
                                >
                                    Updates
                                </div>
                            </div>

                            <div className="person-details">
                                <div className="person-header">
                                    <div className="person-avatar">AK</div>
                                    <div className="person-info">
                                        <h3>Alex Kim</h3>
                                        <div className="person-role">
                                            Lead Developer - Engineering
                                        </div>
                                    </div>
                                </div>

                                <div className="impact-chart">
                                    <div className="impact-label">
                                        <span className="week-label">
                                            Week of Mar 1
                                        </span>
                                        <span className="hours-label">
                                            16 hrs impact
                                        </span>
                                    </div>
                                    <div className="impact-bar">
                                        <div
                                            className="impact-fill"
                                            style={{
                                                width: '40%',
                                                backgroundColor: '#3498db'
                                            }}
                                        ></div>
                                        <div
                                            className="threshold-line"
                                            style={{left: '60%'}}
                                        >
                                            <div className="threshold-label">
                                                Capacity Threshold
                                            </div>
                                        </div>
                                    </div>

                                    <div className="impact-label">
                                        <span className="week-label">
                                            Week of Mar 8
                                        </span>
                                        <span className="hours-label">
                                            24 hrs impact
                                        </span>
                                    </div>
                                    <div className="impact-bar">
                                        <div
                                            className="impact-fill"
                                            style={{
                                                width: '60%',
                                                backgroundColor: '#f39c12'
                                            }}
                                        ></div>
                                        <div
                                            className="threshold-line"
                                            style={{left: '60%'}}
                                        ></div>
                                    </div>

                                    <div className="impact-label">
                                        <span className="week-label">
                                            Week of Mar 15
                                        </span>
                                        <span className="hours-label">
                                            32 hrs impact
                                        </span>
                                    </div>
                                    <div className="impact-bar">
                                        <div
                                            className="impact-fill"
                                            style={{
                                                width: '80%',
                                                backgroundColor: '#e74c3c'
                                            }}
                                        ></div>
                                        <div
                                            className="threshold-line"
                                            style={{left: '60%'}}
                                        ></div>
                                    </div>

                                    <div className="impact-label">
                                        <span className="week-label">
                                            Week of Mar 22
                                        </span>
                                        <span className="hours-label">
                                            28 hrs impact
                                        </span>
                                    </div>
                                    <div className="impact-bar">
                                        <div
                                            className="impact-fill"
                                            style={{
                                                width: '70%',
                                                backgroundColor: '#e74c3c'
                                            }}
                                        ></div>
                                        <div
                                            className="threshold-line"
                                            style={{left: '60%'}}
                                        ></div>
                                    </div>
                                </div>

                                <div className="risk-item">
                                    <div className="risk-title">
                                        Resource Over-allocation
                                    </div>
                                    <div className="risk-desc">
                                        Alex is allocated to both Platform
                                        Migration and Digital Transformation
                                        during the same period.
                                    </div>
                                    <div className="risk-meta">
                                        <span>Reported by: Jessica Lee</span>
                                        <span className="risk-severity high">
                                            High
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </Col>
            </Row>
        </>
    );
};

export default DeliveryImpactDashboard;
